.bp5-key-combo{
  display:flex;
  flex-direction:row;
  align-items:center;
}
.bp5-key-combo > *{
  flex-grow:0;
  flex-shrink:0;
}
.bp5-key-combo > .bp5-fill{
  flex-grow:1;
  flex-shrink:1;
}
.bp5-key-combo::before,
.bp5-key-combo > *{
  margin-right:5px;
}
.bp5-key-combo:empty::before,
.bp5-key-combo > :last-child{
  margin-right:0;
}

.bp5-hotkey-dialog{
  padding-bottom:0;
  top:40px;
}
.bp5-hotkey-dialog .bp5-dialog-body{
  margin:0;
  padding:0;
}
.bp5-hotkey-dialog .bp5-hotkey-label{
  flex-grow:1;
}

.bp5-hotkey-column{
  margin:auto;
  max-height:80vh;
  overflow-y:auto;
  padding:30px;
}
.bp5-hotkey-column .bp5-heading{
  margin-bottom:20px;
}
.bp5-hotkey-column .bp5-heading:not(:first-child){
  margin-top:40px;
}

.bp5-hotkey{
  align-items:center;
  display:flex;
  justify-content:space-between;
  margin-left:0;
  margin-right:0;
}
.bp5-hotkey:not(:last-child){
  margin-bottom:10px;
}
.bp5-icon{
  display:inline-block;
  flex:0 0 auto;
  vertical-align:text-bottom;
}
.bp5-icon:not(:empty)::before{
  content:"" !important;
  content:unset !important;
}
.bp5-icon > svg{
  display:block;
}
.bp5-icon > svg:not([fill]){
