s-heading {{.modifier}}\">H6 heading</h6>\n</div>\n\n.bp5-text-muted - Change text color to a gentler gray.\n\nStyleguide headings\n*/\n.bp5-heading {\n  color: #1c2127;\n  font-weight: 600;\n  margin: 0 0 10px;\n  padding: 0;\n}\n.bp5-dark .bp5-heading {\n  color: #f6f7f9;\n}\n.bp5-heading.bp5-text-muted {\n  color: #5f6b7c;\n}\n.bp5-dark .bp5-heading.bp5-text-muted {\n  color: #abb3bf;\n}\n\n.bp5-running-text h1, h1.bp5-heading {\n  font-size: 36px;\n  line-height: 40px;\n}\n\n.bp5-running-text h2, h2.bp5-heading {\n  font-size: 28px;\n  line-height: 32px;\n}\n\n.bp5-running-text h3, h3.bp5-heading {\n  font-size: 22px;\n  line-height: 25px;\n}\n\n.bp5-running-text h4, h4.bp5-heading {\n  font-size: 18px;\n  line-height: 21px;\n}\n\n.bp5-running-text h5, h5.bp5-heading {\n  font-size: 16px;\n  line-height: 19px;\n}\n\n.bp5-running-text h6, h6.bp5-heading {\n  font-size: 14px;\n  line-height: 16px;\n}\n\n/*\nUI text\n\nMarkup:\n<div class=\"{{.modifier}}\">\n  More than a decade ago, we set out to create products that would transform\n  the way organizations use their data. Today, our products are deployed at\n  the most critical government, commercial, and non-profit institutions in\n  the world to solve problems we hadn’t even dreamed of back then.\n</div>\n\n.bp5-ui-text - Default Blueprint font styles, applied to the `<body>` tag and available as a class for nested resets.\n.bp5-monospace-text - Use a monospace font (ideal for code).\n.bp5-running-text - Increase line height ideal for longform text. See [Running text](#core/typography.running-text) below for additional features.\n.bp5-text-large - Use a larger font size.\n.bp5-text-small - Use a smaller font size.\n.bp5-text-muted - Change text color to a gentler gray.\n.bp5-text-disabled - Change text color to a transparent, faded gray.\n.bp5-text-overflow-ellipsis - Truncate a single line of text with an ellipsis if it overflows its container.\n\nStyleguide ui-text\n*/\n.bp5-ui-text {\n  font-size: 14px;\n  font-weight: 400;\n  letter-spacing: 0;\n  line-height: 1.28581;\n  text-transform: none;\n}\n\n.bp5-monospace-text {\n  font-family: monospace;\n  text-transform: none;\n}\n\n.bp5-text-overflow-ellipsis {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  word-wrap: normal;\n}\n\n/*\nRunning text\n\nMarkup:\n<div class=\"@ns-running-text {{.modifier}}\">\n  <p>\n    We build products that make people better at their most important\n    work — the kind of work you read about on the front page of the\n    newspaper, not just the technology section.\n  </p>\n  <ul>\n    <li>Item the <code>first</code>.</li>\n    <li>Item the <strong>second</strong>.</li>\n    <li>Item the <a href=\"#core/typography.running-text\">third</a>.</li>\n  </ul>\n  <h3>Scale, Speed, Agility</h3>\n  <p>\n    A successful data transformation requires the whole organization — users, the IT shop, and\n    leadership — to operate in lockstep. With Foundry, the enterprise comes together to\n    transform the organization and turn data into a competitive advantage.\n  </p>\n</div>\n\n.bp5-text-large - Use larger font size.\n\nStyleguide running-text\n*/\n.bp5-running-text {\n  font-size: 14px;\n  line-height: 1.5;\n}\n.bp5-running-text h1 {\n  color: #1c2127;\n  font-weight: 600;\n  margin-bottom: 20px;\n  margin-top: 40px;\n}\n.bp5-dark .bp5-running-text h1 {\n  color: #f6f7f9;\n}\n.bp5-running-text h1.bp5-text-muted {\n  color: #5f6b7c;\n}\n.bp5-dark .bp5-running-text h1.bp5-text-muted {\n  color: #abb3bf;\n}\n.bp5-running-text h2 {\n  color: #1c2127;\n  font-weight: 600;\n  margin-bottom: 20px;\n  margin-top: 40px;\n}\n.bp5-dark .bp5-running-text h2 {\n  color: #f6f7f9;\n}\n.bp5-running-text h2.bp5-text-muted {\n  color: #5f6b7c;\n}\n.bp5-dark .bp5-running-text h2.bp5-text-muted {\n  color: #abb3bf;\n}\n.bp5-running-text h3 {\n  color: #1c2127;\n  font-weight: 600;\n  margin-bottom: 20px;\n  margin-top: 40px;\n}\n.bp5-dark .bp5-running-text h3 {\n  color: #f6f7f9;\n}\n.bp5